/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  margin: 0;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 88px;
}

p {
  font-size: 2rem;
  color: #060001;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  border-radius: 15px;
  width: 100%;
  height: auto;
}

.header {
  background-color: #20a281;
}
@media (min-width: 768px) {
  .header.fijo {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #060001;
  }
}
.header h1 {
  color: #f0f2f0;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenedor-header {
  padding: 2rem 0rem;
}
@media (min-width: 768px) {
  .header .contenedor-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  color: #f0f2f0;
  font-size: 2.4rem;
  font-weight: bold;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-bottom: 0;
  }
}
.navegacion-principal a:hover {
  color: #e2e53c;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlate {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(18, 26, 107);
  background: linear-gradient(326deg, rgba(18, 26, 107, 0.7) 0%, rgba(243, 0, 255, 0.6) 100%);
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}
.video .contenido-video h2 {
  position: relative;
  color: #e2e53c;
  margin: 2rem;
  z-index: 0;
}
@media (min-width: 768px) {
  .video .contenido-video h2 {
    margin: 0;
    font-size: 6rem;
  }
}
.video .contenido-video p {
  display: inline-flex;
  background-color: #bd426f;
  font-weight: bold;
  font-size: 2rem;
  padding: 1rem 2rem;
  color: #f0f2f0;
  margin-bottom: 2rem;
  gap: 2rem;
  border-radius: 15px;
  flex-wrap: wrap;
}

.sobre-festival {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 1rem;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
  color: #bd426f;
}
.contenido-festival .fecha {
  font-weight: bold;
  color: #e2e53c;
  background-color: #bd426f;
  padding: 0.5rem 1rem;
  border-radius: 15px;
}
.contenido-festival p {
  line-height: 1.5;
}

.lineup {
  background-color: #bd426f;
  padding: 5rem 0rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #f0f2f0;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 4rem 0 2rem;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #060001;
  font-weight: bold;
}

.bg-amarillo {
  background-color: #e2e53c;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #cfd21c;
}
.bg-amarillo p::after {
  border-radius: 15px;
  background-color: #cfd21c;
}

.bg-verde {
  background-color: #20a281;
}
.bg-verde li:nth-child(odd) {
  background-color: #18775f;
}
.bg-verde p::after {
  border-radius: 15px;
  background-color: #18775f;
}

.escenario {
  padding: 2rem 0;
  border-radius: 15px;
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 1rem 2.5rem;
  font-weight: bold;
}
.calendario span {
  font-weight: lighter;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #f0f2f0;
  color: #060001;
  display: grid;
  place-items: center;
  font-weight: 1000;
  font-size: 3rem;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem;
}
.boletos h3 {
  text-align: center;
  color: #f0f2f0;
}

@media (min-width: 768px) {
  .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contorno-pase {
  border: 2px solid rgba(186, 186, 186, 0.7294117647);
  border-radius: 5px;
}

.pase {
  color: black;
}
.pase .nombre-pase {
  text-align: center;
  color: #f0f2f0;
  font-weight: bold;
  font-size: 3.5rem;
}
.pase-header {
  background-color: #bd426f;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 2rem;
}
.pase-body {
  background-color: #f0f2f0;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li, .pase-body ul :nth-child(odd) {
  background-color: #f0f2f0;
  padding: 1rem 2rem;
  border-bottom: 1px solid rgba(186, 186, 186, 0.7294117647);
}
.pase-footer {
  background-color: #7e74a6;
}
.pase-footer p {
  font-weight: bold;
  font-size: 3rem;
}
.pase-header p, .pase-footer p {
  padding: 0.5rem;
  text-align: center;
  color: #f0f2f0;
  margin: 0;
}

.site-footer {
  background-color: #bd426f;
}
.site-footer p {
  font-weight: 400;
  text-align: center;
  margin: 0;
  padding: 2rem 3rem 1rem;
}
.site-footer p:last-child {
  padding-top: 0;
  text-transform: capitalize;
  font-weight: bold;
}