@use '../base/variables' as v;
@use '../base/mixins' as m;

.galeria{
    padding: 5rem 0;

    h3{
        text-align: center;
    }
}

.galeria-imagenes {
    @include m.resetearLista;
    @include m.grid2(2, 2rem);

    @include m.telefono {
        @include m.grid2(3, 2rem);
    }

    @include m.tablet {
        @include m.grid2(4, 2rem);
    }

    @include m.desktop {
        @include m.grid2(6, 2rem)
    }
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgba($color: #000000, $alpha: 0.8);

    picture {
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 1000;
    font-size: 3rem;
    
    &:hover {
        cursor: pointer;
    }
}
