
@use 'variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid2($columnas, $espacio_columna: 2rem) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espacio_columna;
}

@mixin resetearLista{
    margin: 0;
    padding: 0;
    list-style: none;
}